import React, { Component } from 'react';
import { withRouter } from "react-router-dom";
import { Modal, List, Button, InputItem, Toast } from 'antd-mobile'
import ClipboardJS from 'clipboard'
import axios from 'axios'
import { statusTitles, statusValues, statusImgs, statusColumns } from '../../utils/status'
import copyImg from '../../imgs/icon_copy.png'

const alert = Modal.alert


const data = {
    id: '1',
    status: '待分配',
    city: '广州',
    type: '接机',
    plane: 'A380-1',
    from: '11',
    to: '11',
    time: '11',
    remark: '11',
    submitTime: '11',
    driver: '11',
    phone: '11',
    car: '11',
    startTime: '11',
    endTime: '11'
}

const titles = {
    city: '所属城市',
    type: '类别',
    plane: '航班号',
    from: '出发地',
    to: '目的地',
    time: '预计时间',
    remark: '备注',
    submitTime: '提交时间',
    cancelTime: '取消时间',
    driver: '司机',
    phone: '司机手机号码',
    car: '车牌号',
    startTime: '出发时间',
    endTime: '完成时间'
}

const placeHolder = {
    phone: '请输入司机号码'
}

class DetailPassenger extends Component {
    constructor(props) {
        super(props)
        this.state = {
            id: props.match.params.id,
            // loading: true,
            status: 0,
            content: []
        }
    }


    getClipboard() {
        this.clipboard = new ClipboardJS('.copy-container', {
            text: (trigger) => {
                const { city, type, from, to, time, passenger, passengerPhone } = this.state
                return `${city}，${type}，${from} 至 ${to}，${time}，${passenger}，${passengerPhone}`
            }
        });

        this.clipboard.on('success', function (e) {
            Toast.success('复制成功', 1)
            console.info('Action:', e.action);
            console.info('Text:', e.text);
            console.info('Trigger:', e.trigger);
            e.clearSelection();
        });

        this.clipboard.on('error', function (e) {
            Toast.success('复制失败', 1)
            console.error('Action:', e.action);
            console.error('Trigger:', e.trigger);
        });
    }

    componentWillUnmount() {
        this.clipboard.destroy()
    }
    componentDidMount() {
        this.getClipboard()
        axios(`/order/${this.state.id}`).then(data => {
            let {
                cancle_time, category, city, city_id, complete_time, driver = {},
                estimated_time, flight, passenger, place_from, place_to, remark,
                start_time, status, submit_time, _id
            } = data
            passenger = passenger || {}
            driver = driver || {}
            city = city || {}
            if (_id) {
                const targetData = {
                    // id: _id,
                    status,
                    city: city.name,
                    type: category,
                    plane: flight,
                    from: place_from,
                    to: place_to,
                    time: estimated_time,
                    remark,
                    submitTime: submit_time,
                    passenger: passenger.name,
                    passengerPhone: passenger.phone,
                    driver: driver.name,
                    driverPhone: driver.phone,
                    car: driver.card_number,
                    startTime: start_time,
                    endTime: complete_time
                }

                const targetColumns = statusColumns[status]


                const content = Object.keys(targetColumns).map(item => item.includes('borderLine') ?
                    { key: item } : {
                        key: item,
                        title: targetColumns[item],
                        value: targetData[item],
                    })
                if (status === 0) {
                    content.push({ key: 'borderLine' }, { key: 'driverPhone', title: '司机手机号' })
                }
                this.setState({ content, ...targetData })
            } else {
                this.setState({ status: undefined })
            }

        }).catch(e => console.log(e))
            .finally(() => {
                // this.setState({ loading: false })
            })
    }
    render() {
        const { id, status, content } = this.state
        return <div className='detail-content-container'>
            <div
                className='copy-container'
                style={{ position: 'fixed', top: '3.5vw', right: '5vw', textAlign: 'center' }}
                onClick={() => { }}
            >
                <img style={{ width: '6vw' }} src={copyImg} onClick={() => { }} />
                <div style={{fontSize:'3vw'}}>拷贝</div>
            </div>
            <div className='detail-content-head'>
                <img src={statusImgs[status]} />
                <div>{statusTitles[status]}</div>
            </div>
            <div className='detail-content-subhead'>
                行程信息
            </div>
            {content.map(({ title, value, key, placeHolder }, i) => <div key={i}>
                {key.includes('borderLine') ?
                    <div className='detail-content-line-item' />
                    :
                    <div className={`detail-content-item`}>
                        <div className='detail-content-item-left'>{title}</div>
                        <div className='detail-content-item-right'>{
                            status === 0 && key === 'driverPhone'
                                ?
                                <List>
                                    <InputItem
                                        placeholder={placeHolder}
                                        // value={this.state[key]}
                                        onChange={value => this.setState({ [key]: value })}
                                    />
                                </List>
                                :
                                value
                        }</div>
                    </div>}
            </div>)}
            {status === 0 && <Button
                type='primary'
                loading={this.state.buttonLoading}
                className='detail-content-button'
                onClick={() => {
                    this.setState({ buttonLoading: true })
                    axios.get(`/driver/phone/${this.state.driverPhone}`).then(data => {
                        const { card_number, name, phone, _id } = data
                        if (card_number) {
                            alert(
                                '请核对司机信息',
                                <div className='in-am-modal-content'>
                                    {[
                                        { title: '手机号', value: phone },
                                        { title: '姓名', value: name },
                                        { title: '车牌号', value: card_number },
                                    ].map(({ title, value, key }, i) => <div key={i}>
                                        <div className='in-am-modal-content-left'>{title}</div>
                                        <div className='in-am-modal-content-right'>{value}</div>
                                    </div>)}
                                </div>,
                                [
                                    { text: '取消', onPress: () => { } },
                                    {
                                        text: '确定', onPress: () => new Promise(resolve => axios.post(`/order/statu/${id}`, {
                                            get_type: 1, driver_id: _id, status: 1
                                        }).then(data => {
                                            resolve()
                                            Toast.success('分配成功！')
                                            this.props.history.go(-1)
                                        }).catch(e => {
                                            console.log(e)
                                            resolve()
                                        }))
                                    },
                                ]
                            )
                        } else {
                            Toast.fail('很抱歉,该司机不存在或未审核通过,请重新分配司机', 2)
                        }

                    }).catch(e => console.log(e)).finally(() => this.setState({ buttonLoading: false }))

                }}
            >分配</Button>}
        </div>



    }

}

export default withRouter(DetailPassenger)
