import React, { Component } from 'react';
import { withRouter } from "react-router-dom";
import backIcon from '../imgs/icon_return.png'


class Header extends Component {
    render() {
        return <header style={{
            position: 'fixed', top: 0, left: 0, width: '100%',
            overflow: 'hidden', padding: '7vw 5vw'
        }}>

            
            <span style={{ float: 'left' }} onClick={() => this.props.history.go(-1)}>
                <img src={backIcon} style={{ width: '5vw' }} />
            </span>
            <div style={{ textAlign: 'center' }}>{this.props.title}</div>
        </header>
    }

}

export default withRouter(Header)