import React, { Component } from 'react'
import { withRouter, Link } from "react-router-dom"
import { List, InputItem, WhiteSpace, Toast, Button } from 'antd-mobile'
import axios from 'axios'


class Login extends Component {
    state = {
        loading: false
    }
    render() {
        return <div style={{ padding: '3vw' }}>
            <div className='login-page-head'>
                <img className='login-page-head-icon' src={require('../../imgs/icon.png')} />
                <div className='login-page-head-title'>易航达</div>
                <img className='login-page-head-bg' src ={require('../../imgs/img_title_bg.png')} />
            </div>
            <List renderHeader={() => '账号'}>
                <InputItem
                    clear
                    // type="phone"
                    placeholder="请输入账号"
                    onChange={account => this.setState({ account })}

                ></InputItem>
            </List>
            <WhiteSpace style={{ height: '3vw' }} />
            <List renderHeader={() => '密码'}>
                <InputItem
                    clear
                    type='password'
                    placeholder="请输入密码"
                    onChange={password => this.setState({ password })}
                    error={false}
                    onErrorClick={() => Toast.info('密码长度不得小于四位')}
                ></InputItem>
            </List>
            <Button
                type='primary'
                className='login-page-button'
                onClick={() => {
                    const { account, password } = this.state
                    this.setState({ loading: true })
                    axios.post('/login', { login_type: 2, account, password }).then(data => {
                        const { token, info } = data
                        sessionStorage.setItem('user-info', JSON.stringify({ token, name: info.name, icon: info.url, city: info.city }))
                        this.props.history.push('/home')
                    }).catch(e => console.log(e)).finally(() => this.setState({ loading: false }))
                }}
            >
                登录
            </Button>

        </div>
    }
}

export default withRouter(Login);
