import React, { Component } from 'react'
import { withRouter } from "react-router-dom"
import logoutImg from '../imgs/icon_dropout.png'
import listImg from '../imgs/icon_list.png'



class HomeHeader extends Component {
    render() {
        const {iconUrl} = this.props
        const userInfo = sessionStorage.getItem('user-info')
        const { name, city, icon } = JSON.parse(userInfo) || {}
        return <header style={{
            position: 'fixed', top: '9vw', left: 0, width: '100%',
            overflow: 'hidden', padding: '4vw'
        }}>
            <span style={{ display: 'inline-block', verticalAlign: 'middle', width: '80%' }}>
                <img src={icon}
                    style={{
                        display: 'inline-block', verticalAlign: 'middle',
                        width: '16vw', height: '16vw', borderRadius: '16vw', border: '1px solid rgba(0,0,0,0.1)'
                    }} />
                <span style={{ display: 'inline-block', verticalAlign: 'middle', marginLeft: '3vw' }}>
                    <span style={{ display: 'block', fontWeight: 600 }}>{name}</span>
                    {city && <span style={{ display: 'block', fontWeight: 600 }}>{city.name}</span>}
                </span>
            </span>
            <span
                style={{ display: 'inline-block', verticalAlign: 'middle', width: '10%', textAlign: 'center' }}
            >
                {iconUrl && <div onClick={()=>this.props.history.push(iconUrl)}>
                    <img src={listImg} style={{ width: '5vw', height: '5vw' }} />
                    <div style={{ fontSize: '3vw' }}>行程</div>
                </div>}

            </span>
            <span
                style={{ display: 'inline-block', verticalAlign: 'middle', width: '10%', textAlign: 'center' }}
                onClick={() => {
                    sessionStorage.removeItem('user-info')
                    this.props.history.push('/login')
                }}
            >
                <img src={logoutImg} style={{ width: '5vw', height: '5vw' }} />
            </span>

        </header>
    }

}

export default withRouter(HomeHeader);
