import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Link, Redirect } from "react-router-dom";
import HomeHeader from './component/home-header'
import DetailHeader from './component/detail-header'
import Login from './page/leader/login'
import HomePassenger from './page/leader/home-passenger'
import DetailPassenger from './page/leader/detail-passenger'





class App extends Component {
  constructor(props) {
    super(props)
    this.state = {

    }
  }

  render() {
    const { name } = this.state
    return <Router>
      <Route path='/' render={({ history }) => {
        this.props.getHistory(history)
        return <div className='route-container'>
          <Route path="/login" render={() => <Login setUserInfo={this.setUserInfo} />} />
          <Route exact path="/" render={() => <Redirect to={sessionStorage.getItem('user-info') ? '/home' : '/login'} />} />
          <Route exact path="/home" render={() => <Redirect to={'/home/passenger'} />} />
          <Route path="/home" render={() => <div className='home-container background-home'>
            <HomeHeader name={name} />
            <div className='home-content-scroll home-content-no-foot'>
              <div className='home-content'>
                <Route path="/home/passenger" render={() => <HomePassenger />} />
              </div>
            </div>
            {/* <Footer /> */}
          </div>} />
          <Route path="/detail" render={() => <div className='detail-container background-map'>
            <DetailHeader />
            <div className={'detail-content-scroll'}>
              <div className={'detail-content'}>
                <Route path="/detail/passenger/:id" render={() => <DetailPassenger />} />
              </div>
            </div>
          </div>} />
        </div>
      }} />
    </Router>
  }

}

export default App;
