import React, { Component } from 'react'
import { withRouter } from "react-router-dom"
import axios from 'axios'
import querystring from 'querystring'
import { Icon } from 'antd-mobile'
import { statusTitles, statusValues } from '../../utils/status'
import cityImg from '../../imgs/icon_positioning.png'
import fromImg from '../../imgs/icon_outset.png'
import toImg from '../../imgs/icon_destination.png'
import timeImg from '../../imgs/icon_time.png'
import planeImg from '../../imgs/icon_flight.png'
import dotImg from '../../imgs/dot.png'

const headData = [{ title: '总共', value: 0 }].concat(statusTitles.map(title => ({ title, value: 0 })))

class Passenger extends Component {
    constructor(props) {
        super(props)
        this.state = {
            list: [],
            page: 0,
            pageSize: 10,
            activeStatus: -1
        }
    }
    componentDidMount() {
        this.getList()
        this.scroll = document.querySelector('.home-content-scroll')
        this.scroll.addEventListener('scroll', this.handleScroll.bind(this))
    }

    componentWillUnmount() {
        this.scroll.removeEventListener('scroll', this.handleScroll.bind(this))
    }

    handleScroll() {
        const { scrollHeight, offsetHeight, scrollTop } = this.scroll
        // console.log(scrollHeight, offsetHeight + scrollTop - 50)
        if (offsetHeight + scrollTop > scrollHeight - 50) {
            this.getList()
        }

    }

    getIsEnd() {
        const { loading, list, activeStatus, fetched, total_count } = this.state
        return fetched ? headData[activeStatus + 1].value == list.length : false
    }

    getList(page = this.state.page) {
        const { pageSize, loading, list, activeStatus, fetched } = this.state
        if (!loading && !this.getIsEnd()) {
            this.setState({ loading: true })
            const fetchPage = page + 1
            const params = { page: fetchPage, pageSize, get_type: 1 }
            if (activeStatus >= 0) {
                params.status = activeStatus
            }
            axios.get(`/order/list?${querystring.stringify(params)}`)
                .then(data => {
                    // console.log(data)
                    const { datas, total_count, count } = data
                    headData.forEach((item, i) => {
                        item.value = i === 0 ? total_count : data[`state_${i - 1}_count`]
                    })
                    const fetchedList = datas.map(({ category, city, flight, place_from, place_to, status, submit_time, _id }) => ({
                        id: _id,
                        city: city.name,
                        type: category,
                        plane: flight,
                        status,
                        from: place_from,
                        to: place_to,
                        time: submit_time,
                    }))
                    this.setState({
                        page: fetchPage,
                        list: list.concat(fetchedList)
                        // list: page > 0 ? list.concat(fetchedList) : fetchedList
                    })
                }).catch(e => console.log(e)).finally(() => this.setState({ fetched: true, loading: false }))
        }
    }





    render() {
        const { list, activeStatus, loading, fetched } = this.state
        const isEnd = this.getIsEnd()
        return <div>
            <div className='home-content-head'>
                {headData.map(({ title, key, value }, i) => <div
                    key={i}
                    className="home-content-head-item"
                    onClick={() => {
                        const activeStatus = i - 1
                        if (!loading && activeStatus !== this.state.activeStatus) {
                            this.setState({ activeStatus, list: [], fetched: false }, this.getList.bind(this, 0))
                        }
                    }}
                >
                    {activeStatus === i - 1 && <img src={dotImg} className="home-content-head-item-dot" />}
                    <div className="home-content-head-item-num">{value}</div>
                    <div className="home-content-head-item-status">{title}</div>
                </div>)}
            </div>
            {list.map(({ id, city, type, plane, status, from, to, time }, i) => <div
                key={i}
                className='home-content-passenger-item'
                onClick={() => this.props.history.push(`/detail/passenger/${id}`)}
            >
                <div className='home-content-passenger-item-head'>
                    <img src={cityImg} className='home-content-passenger-item-head-icon-city' />
                    <span className='home-content-passenger-item-head-city'>
                        {city}
                    </span>
                    <span className='home-content-passenger-item-head-type'>{type}</span>
                    <img src={planeImg} className='home-content-passenger-item-head-icon-plane' />
                    <span className='home-content-passenger-item-head-plane'>
                        航班号 : {plane}
                    </span>
                    <span className={`home-content-passenger-item-head-status home-${statusValues[status]}-status-color`}>
                        {statusTitles[status]}
                    </span>
                </div>
                <div className='home-content-passenger-item-child'>
                    <img src={fromImg} />{from}
                </div>
                <div className='home-content-passenger-item-childline'><span>|</span></div>
                <div className='home-content-passenger-item-child'>
                    <img src={toImg} />{to}
                </div>
                <div></div>
                <div className='home-content-passenger-item-child home-content-passenger-item-child-last' >
                    <img src={timeImg} /> {time}
                </div>
            </div>)}
            <div className='home-content-foot'>
                {isEnd ? '没有更多内容' : <Icon type='loading' />}
            </div>
        </div>


    }

}

export default withRouter(Passenger)
